@font-face {
  font-family: "Karla";
  src: local("Karla"),
    url("./assets/fonts/Karla-VariableFont_wght.ttf") format("truetype");
}

@font-face {
  font-family: "Markazi Text";
  src: local("Markazi Text"),
    url("./assets/fonts/MarkaziText-VariableFont_wght.ttf") format("truetype");
}

html {
  font-family: Karla;
  font-size: 16px;
  font-weight: var(--font-weight-regular);
  color: var(--secondary-black);
}

.display-title {
  font-family: Markazi Text;
  font-size: 64px;
  font-weight: var(--font-weight-medium);
}

.sub-title {
  font-family: Markazi Text;
  font-size: 40px;
  font-weight: var(--font-weight-regular);
}

.lead-text {
  font-family: Karla;
  font-size: 18px;
  font-weight: var(--font-weight-medium);
}

.section-title {
  font-family: Karla;
  font-size: 20px;
  font-weight: var(--font-weight-extra-bold);
}

.section-category {
  font-family: Karla;
  font-size: 16px;
  font-weight: var(--font-weight-extra-bold);
}

.card-description {
  font-family: Karla;
  font-size: 16px;
  line-height: 1.5;
  font-weight: var(--font-weight-regular);
}

.card-title {
  font-family: Karla;
  font-size: 18px;
  font-weight: var(--font-weight-bold);
}

.highlight-text {
  font-family: Karla;
  font-size: 16px;
  font-weight: var(--font-weight-medium);
}

html,
body {
  margin: 0;
  padding: 0;
}

input[type="text"],
input[type="email"],
input[type="date"],
input[type="time"],
select,
textarea {
  border-style: solid;
  border-width: 2px;
  border-color: var(--primary-green);
  outline: none;
  font-family: karla;
  font-size: 16px;
  font-weight: var(--font-weight-regular);
  border-radius: 16px;
  background-color: white;
  resize: none;
  padding: 5px;
}

select {
  height: 35px;
}

input[type="date"],
input[type="time"] {
  display: flex;
  justify-content: center;
}

input[type="text"],
input[type="email"],
textarea {
  border-radius: 8px;
}

input[type="date"],
input[type="time"],
select {
  text-align: center;
}

:root {
  --global-margin: 0 auto;
  --global-padding: 0;
  --primary-green: #495e57;
  --primary-yellow: #f4ce14;
  --secondary-grey: #edefee;
  --secondary-black: #333;
  --secondary-color-one: #ee9972;
  --secondary-color-two: #fbdabb;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  --font-weight-extra-bold: 800;
}

.outer-layout > * {
  width: var(--global-width);
  margin: var(--global-margin);
  padding: var(--global-padding);
}

.lemon-button {
  cursor: pointer;
  width: 206px;
  height: 52px;
  border-radius: 16px;
  border: none;
  outline: none;
  color: inherit;
  background-color: var(--primary-yellow);
  font-family: Karla;
  font-size: 20px;
  font-weight: var(--font-weight-bold);
}

.lemon-button:active {
  color: white;
  background-color: var(--primary-green);
}

.lemon-button:disabled {
  color: inherit;
  background-color: var(--secondary-color-one);
  cursor: not-allowed;
}

@media (min-width: 905px) {
  :root {
    --global-width: 900px;
  }
}

@media (max-width: 905px) {
  :root {
    --global-padding: 0 24px;
  }
}
