.footer-background {
  background-color: var(--primary-green);
  padding: 76px 0 111px;
}

.footer {
  display: flex;
  justify-content: space-between;
}

.footer .logo {
  padding-right: 90px;
}

.footer menu,
.footer .contact-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.footer menu {
  list-style-type: none;
  padding: 0;
}

.footer h2 {
  font-family: Karla;
  font-size: 18px;
  font-weight: var(--font-weight-bold);
  color: white;
}

.footer .contact-info,
a {
  color: white;
}

.footer a:link,
.footer a:visited {
  text-decoration: none;
}

.footer .contact-info {
  font-family: Karla;
  font-size: 18px;
  font-weight: var(--font-weight-regular);
}

@media (max-width: 860px) {
  .footer-background {
    padding: 20px 0 80px;
  }

  .footer {
    flex-direction: column;
    align-items: flex-start;
  }

  .footer > :first-child {
    align-self: center;
  }

  .footer .logo {
    padding-right: 0;
    padding-bottom: 10px;
  }
}
